import React, { useState } from "react";
import cloud from "./assets/cloud.png";

const PlayerCard = ({ onAddSong }) => {
  const [isModalOpen, setIsModalOpen] = useState(false);
  const [newSong, setNewSong] = useState({ title: "", artist: "" });

  const handleAddSong = () => {
    if (newSong.title.trim() && newSong.artist.trim()) {
      onAddSong(newSong);
      setNewSong({ title: "", artist: "" });
      setIsModalOpen(false);
    }
  };

  return (
    <div
      className="px-6 py-4 rounded-xl flex flex-col space-y-6 items-center relative w-full bg-white border-2 border-blue-300"
      style={{
        boxShadow: "6px 6px 0px rgba(147, 197, 253, 1)",
      }}
    >
      {/* Karaoke Glow Title */}
      <div className="text-center z-10">
        <p className="text-2xl font-bold text-blue-700">
          Pretender - Acoustic Ver.
        </p>
        <p className="text-blue-400 text-sm">By Official HIGE DANDISM</p>
      </div>

      {/* Scaling Sound Wave Animation */}
      <div className="flex items-center space-x-2 z-10">
        {[3, 2, 5, 6, 4, 6, 3, 2, 5].map((height, index) => (
          <div
            key={index}
            style={{
              height: `${height * 10}px`,
              animationDelay: `${index * 100}ms`,
            }}
            className="w-1 bg-gradient-to-t from-blue-600 to-purple-600 animate-wave rounded-md"
          ></div>
        ))}
      </div>

      {/* Controls */}
      <div className="flex items-center space-x-6 z-10">
        {[
          { icon: "bi-shuffle", color: "text-blue-300" },
          { icon: "bi-skip-backward-fill", color: "text-blue-300" },
          {
            icon: "bi-play-circle-fill",
            color: "text-blue-500",
            size: "text-4xl",
            action: () => setIsModalOpen(true),
          },
          { icon: "bi-skip-forward-fill", color: "text-blue-300" },
          { icon: "bi-repeat", color: "text-blue-300" },
        ].map(({ icon, color, size, action }, idx) => (
          <button
            key={idx}
            onClick={action} // Menangani klik tombol
            className={`${color} transition-transform transform hover:scale-110`}
          >
            <i className={`bi ${icon} ${size || "text-xl"}`}></i>
          </button>
        ))}
      </div>

      {/* Image */}
      <img
        src={cloud}
        alt="Decoration"
        className="absolute -bottom-14 -right-20 w-60 h-30 object-contain"
      />

      {/* Modal */}
      {isModalOpen && (
        <div className="fixed inset-0 flex items-center justify-center z-50">
          <div className="bg-white rounded-lg p-6 w-96 shadow-lg">
            <h4 className="text-lg font-semibold text-blue-500 mb-4">
              Add a New Song
            </h4>
            <div className="flex flex-col gap-2">
              <input
                type="text"
                value={newSong.title}
                onChange={(e) =>
                  setNewSong({ ...newSong, title: e.target.value })
                }
                placeholder="Song Title"
                className="border px-2 py-1 rounded w-full"
              />
              <input
                type="text"
                value={newSong.artist}
                onChange={(e) =>
                  setNewSong({ ...newSong, artist: e.target.value })
                }
                placeholder="Artist Name"
                className="border px-2 py-1 rounded w-full"
              />
              <div className="flex justify-end gap-2 mt-4">
                <button
                  onClick={() => setIsModalOpen(false)} // Tutup modal
                  className="bg-gray-500 text-white px-4 py-2 rounded"
                >
                  Cancel
                </button>
                <button
                  onClick={handleAddSong} // Tambahkan lagu
                  className="bg-blue-500 text-white px-4 py-2 rounded"
                >
                  Add Song
                </button>
              </div>
            </div>
          </div>
        </div>
      )}
    </div>
  );
};

export default PlayerCard;
