import React, { useState } from "react";
import PlayerCard from "./PlayerCard";
import cloud from "./assets/cloud.png";

const Queue = () => {
  const [songs, setSongs] = useState([
    { id: 1, title: "Song 1", artist: "Artist 1" },
    { id: 2, title: "Song 2", artist: "Artist 2" },
    { id: 3, title: "Song 3", artist: "Artist 3" },
  ]);

  const [editSongId, setEditSongId] = useState(null);
  const [editSong, setEditSong] = useState({ title: "", artist: "" });

  // Fungsi untuk menambahkan lagu ke antrean
  const addSongToQueue = (newSong) => {
    setSongs([...songs, { id: Date.now(), ...newSong }]);
  };

  // Fungsi untuk mulai mengedit lagu
  const startEditSong = (id) => {
    const songToEdit = songs.find((song) => song.id === id);
    setEditSongId(id);
    setEditSong({ title: songToEdit.title, artist: songToEdit.artist });
  };

  // Fungsi untuk menyimpan perubahan lagu
  const saveEditSong = () => {
    setSongs(
      songs.map((song) =>
        song.id === editSongId ? { ...song, ...editSong } : song
      )
    );
    setEditSongId(null);
    setEditSong({ title: "", artist: "" });
  };

  // Fungsi untuk menghapus lagu dari antrean
  const deleteSongFromQueue = (id) => {
    setSongs(songs.filter((song) => song.id !== id));
  };

  return (
    <div className="flex flex-col items-center space-y-6 w-full relative">
      {/* PlayerCard */}
      <PlayerCard onAddSong={addSongToQueue} />

      {/* Queue List */}
      <div
        className="w-full bg-white px-4 py-6 rounded-xl shadow-lg border-2 border-blue-300"
        style={{
          boxShadow: "6px 6px 0px rgba(147, 197, 253, 1)",
        }}
      >
        <h3 className="text-xl font-bold text-blue-500 mb-4">Up Next</h3>
        <ul>
          {songs.map((song) => (
            <li
              key={song.id}
              className="relative flex justify-between items-center mb-3 text-blue-600 group"
            >
              {editSongId === song.id ? (
                <div className="flex flex-col w-full gap-2">
                  <input
                    type="text"
                    value={editSong.title}
                    onChange={(e) =>
                      setEditSong({ ...editSong, title: e.target.value })
                    }
                    placeholder="Edit Title"
                    className="border px-2 py-1 rounded w-full"
                  />
                  <input
                    type="text"
                    value={editSong.artist}
                    onChange={(e) =>
                      setEditSong({ ...editSong, artist: e.target.value })
                    }
                    placeholder="Edit Artist"
                    className="border px-2 py-1 rounded w-full"
                  />
                  <div className="flex justify-end gap-2 mt-2">
                    <button
                      onClick={saveEditSong}
                      className="bg-green-500 text-white px-3 py-1 rounded"
                    >
                      Save
                    </button>
                    <button
                      onClick={() => setEditSongId(null)}
                      className="bg-gray-500 text-white px-3 py-1 rounded"
                    >
                      Cancel
                    </button>
                  </div>
                </div>
              ) : (
                <div
                  className="flex items-center justify-between p-2 bg-gradient-to-br from-indigo-100 via-transparent to-blue-100 rounded-lg transition-shadow border border-blue-100 w-full"
                  style={{
                    boxShadow: "4px 4px 0px rgba(147, 197, 253, 0.4)",
                  }}
                >
                  <div className="flex items-center space-x-2 text-blue-500">
                    <i className="bi bi-play-fill text-lg"></i>
                    <p className="font-medium">{song.title}</p>
                  </div>
                  <div className="flex transition-all">
                    <p className="text-blue-500">{song.artist}</p>
                    <div
                    className="flex items-center justify-end overflow-hidden group-hover:w-12 transition-all duration-300 w-0"
                  >
                    <button
                      onClick={() => startEditSong(song.id)}
                      className="text-blue-400 ml-2"
                    >
                      <i className="bi bi-pencil-square"></i>
                    </button>
                    <button
                      onClick={() => deleteSongFromQueue(song.id)}
                      className="text-red-600 ml-2"
                    >
                      <i class="bi bi-x-circle"></i>
                    </button>
                  </div>
                  </div>
                </div>
              )}
            </li>
          ))}
        </ul>
      </div>

      {/* Image */}
      <img
        src={cloud}
        alt="Decoration"
        className="absolute -bottom-28 -right-20 w-60 h-60 object-contain"
      />
    </div>
  );
};

export default Queue;
